.article {
  color: #42474d;

  &-wrapper {
    max-width: 865px;
    margin: 0 auto;
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
  }

  & h1, h2 {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 100%;
    letter-spacing: 0.02em;
    margin-bottom: 12px;

    @media (min-width: $md) {
      font-size: 2.5rem;
      line-height: 130%;
      margin-bottom: 24px;
    }
  }
  & h3, h4, h5 {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 100%;
    letter-spacing: 0.02em;
    margin-bottom: 12px;

    @media (min-width: $md) {
      font-size: 1.75rem;
      line-height: 130%;
      margin-bottom: 24px;
    }
  }

  & p {
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 130%;

    @media (min-width: $md) {
      font-size: 1.25rem;
    }
  }

  & img {
    max-width: 100%;
    height: auto;
  }

  & figure {
    display: block;
    margin: 20px auto;
    border-radius: 1rem;
    overflow: hidden;
  }

  & ul, ol {
    font-weight: 400;
    font-size: 1rem;
    line-height: 130%;
    padding-left: 1rem;

    @media (min-width: $xs) {
      font-size: 1.25rem;
      padding-left: 1.5rem;
    }
  }

  & a {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #398ef0;

    transition: 0.3s;

    &:hover {
     background-color: #398ef0;
      color: #fff;
    }
    &:visited {
      color: #8d46d9;
    }
  }

  & .wrapper-table {
    width: 100%;
    margin: 1rem 0;
    overflow-x: auto;
  }

  & table {
    border-collapse: collapse;
    margin: 1rem auto;
    vertical-align: top;

    @media (min-width: $xs) {
      width: 100%;
      margin: 3rem auto;
    }

    & tr {
      &:not(:last-child) {
        border-bottom-width: 1px;
      }
    }

    & td {
      padding: 1rem;
      text-align: center;

      font-family: $mainFontMedium;
      font-weight: 500;
      font-size: 0.75rem;
      line-height: 130%;

      &:not(:last-child) {
        border-right-width: 1px;
      }

      @media (min-width: $md) {
        padding: 1.5rem;
        font-size: 1.25rem;
      }
    }
  }

  & .block {
    padding: 1rem;

    @media (min-width: $md) {
      padding: 1.5rem;
    }

    &--yellow {
      background: #fffeda;

      @media (min-width: $md) {
        border-radius: 1rem;
        box-shadow: -8px -8px 10px 0 #fff, 8px 8px 16px 0 rgba(73, 76, 79, 0.19);
      }
    }

    & li {
      margin-bottom: 10px;

      @media (min-width: $xs) {
        margin-bottom: 20px;
      }
    }
  }

  & .ul-list {
    list-style-type: disc;
  }

  & .ol-list {
    position: relative;
    list-style-type: none;

    & li {
      position: relative;
      padding-left: 0.5rem;

      &::before {
        content: attr(data-li);
        position: absolute;
        top: 0;
        left: 0;
        transform: translateX(-100%);

        font-family: $mainFontBold;
        font-weight: 700;
        font-size: 1.2rem;
        line-height: 130%;
      }
    }
  }

  & .link-red {
    display: inline-block;
    margin: 20px auto;
    vertical-align: center;
    border-radius: 1rem;
    padding: 0.62rem 1rem 0.75rem;
    color: #fff;
    text-decoration: none;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.25rem;
    line-height: 130%;

    box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.25);
    background: #cd4141;

    transition: box-shadow 0.3s ease-in-out, background 0.3s ease-in-out, color 0.3s ease-in-out, transform 0.3s ease-in-out;
    cursor: pointer;

    @media (min-width: $sm) {
      padding: 15px 22px;
      margin: 40px auto;
    }

    &:hover {
      box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.3);
      background: #ba3838;
      color: #fff;
      transform: scale(1.1);
    }

    &:visited {
      box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.3);
      background: #ba3838;
      color: #fff;
    }
  }

  & .sale-block {
    display: flex;
    flex-direction: column;
    margin: 1rem 0;

    @media (min-width: $md) {
      justify-content: center;
      margin: 2rem 0 1rem;
    }

    &__btn {
      width: 100%;
      text-align: center;
      padding: 1rem;

      @media (min-width: $xs) {
        width: auto;
      }
    }
  }
}

