.links {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @media (min-width: $sm) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 0;
  }

  &__list {
    @include listReset();

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-transform: uppercase;

    width: 100%;

    @include ff(
      'font-size',
      (
        375px: 17px,
        768px: 20px,
      )
    );

    @media (min-width: $sm) {
      width: 50%;
    }

    @media (min-width: $xxl) {
      padding-top: 4px;
      padding-left: 50px;
    }

    & li {
      margin-bottom: 0.77rem;

      @media (min-width: $xs) {
        margin-bottom: 0.7rem;
      }

      & span {
        width: 86%;
        @media (min-width: $sm) {
          width: 80%;
        }
        @media (min-width: $md) {
          width: 90%;
        }
      }
    }

    & a {
      display: block;
    }

    & .icon {
      @media (min-width: $sm) {
        margin-right: 1rem;
      }
      @media (min-width: $md) {
        margin-right: 0.7rem;
      }
    }
  }
}
