.location {
  z-index: 10;
  position: absolute;
  // top: -235px;
  top: 10%;
  left: 5px;
  background-color: $yellow-color;
  border-radius: 14px;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  max-width: 310px;

  padding: 1rem 1rem 1.3rem 1.4rem;

  // @media (min-width: 360px) {
  //   top: -244px;
  // }
  // @media (min-width: 400px) {
  //   top: -250px;
  // }
  @media (min-width: $xs) {
    // top: 0;
    top: 100%;
    left: 16px;
  }

  @media (min-width: $md) {
    // top: 109px;
    left: 20px;
    // top: -15px;

    padding: 1.5rem 1rem 1.6rem 1.4rem;
  }

  @media (min-width: $xl) {
    left: 80px;
    // top: -30px;
    // top: 0;
  }

  &__title {
    width: 100%;
    margin-bottom: 0.8rem;
    font-family: $mainFontBold;
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
    color: $primary-color;

    text-align: center;

    @media (min-width: $md) {
      margin-bottom: 1.1rem;
    }
  }

  & .button {
    font-size: 20px;
    color: $primary-color;
    border: 1px solid $primary-color;
    border-radius: 6px;
    margin: 0 0.5rem;
    padding: 0.6rem 2.15rem;
    transition: $transition-base;

    &:hover {
      background-color: $primary-color;
      border-color: $primary-color;
      color: $white-color;
    }

    &.active {
      background-color: $primary-color;
      color: $white-color;
    }
  }
}
