.article-meta {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
  padding: 0;
  width: 100%;

  @media (min-width: $xs) {
    padding: 1.25rem;
    flex-direction: row;
    align-items: flex-end;
    box-shadow: 8px 8px 16px 0 rgba(73, 76, 79, 0.19);
    background: linear-gradient(139deg, #f9f9f9 0%, #fff 100%);
  }

  &__author {
    display: flex;
    align-items: center;
    gap: 0.75rem;
  }

  &__avatar {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;

    img {
      margin: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__name {
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: #42474d;
    margin-bottom: 0.25rem;
  }

  &__position {
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: #42474d;
  }

  &__date {
    font-weight: 700;
    font-size: 1rem;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: #42474d;
    flex-shrink: 0;
  }

  &__rating-date {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    @media (min-width: $xs) {
      flex-direction: row;
    }
  }

  // Стили для рейтинга
   &__rating {
    display: flex;
    align-items: center;
    gap: 0.25rem;
  }

  &__rating-value {
    font-size: 0.75rem;
    color: #666;
    margin-left: 0.5rem;
    font-weight: 600;
  }



  &__star {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  transition: transform 0.2s ease;

  &:hover {
    transform: scale(1.1);
  }

  &--filled path {
    fill: #42474c;
  }

  &--empty path {
    fill: #e0e0e0;
  }

  &--half {
    // Просто делаем звезду заполненной, если маски не поддерживаются
    path {
      fill: #42474c;
      opacity: 0.7;
    }
  }
}
}
