.title {
  font-family: $mainFontBold;
  font-weight: 700;
  line-height: 130%;

  @include ff(
      'font-size',
      (
        375px: 16px,
        1280px: 20px,
      )
    );

    & span {
      word-spacing: inherit;
    }
}
.subtitle {
  display: inline-block;
  font-family: $mainFont;
  font-weight: 400;
  line-height: 130%;

  @include ff(
      'font-size',
      (
        375px: 16px,
        1280px: 20px,
      )
    );

    & span {
      word-spacing: inherit;
    }
}
