.page {
  &--overlay {
    overflow: hidden;
  }
}

.page-title-icon {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;

  color: $primary-color;

  @media (min-width: $sm) {
    justify-content: flex-start;
    width: auto;
    color: rgba(73, 76, 79, 0.5);
  }

  & h1 {
    margin: 0;
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 22px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: inherit;

    @media (min-width: 350px) {
      font-size: 22px;
    }
    @media (min-width: 375px) {
      font-size: 23px;
    }
    @media (min-width: $xs) {
      font-size: 26px;
    }
    @media (min-width: $sm) {
      font-size: 50px;
    }
    @media (min-width: $md) {
      font-size: 60px;
    }
  }

  & .icon {
    margin-top: 3px;
    width: 20px;
    height: 18px;
    fill: rgba(73, 76, 79, 0.5);

    @media (min-width: 375px) {
      margin-top: 6px;
      width: 30px;
    }
    @media (min-width: $xs) {
      width: 30px;
      height: 23px;
    }
    @media (min-width: $sm) {
      margin-left: 0.5rem;
      margin-top: 15px;
      width: 30px;
      height: 37px;
    }
    @media (min-width: $md) {
      margin-top: 10px;
      margin-left: 0.5rem;
      height: 60px;
    }
  }

  & .small {
    display: block;
    font-size: .75rem;
    line-height: 100%;
    color: $gray-color;

    @media (min-width: $sm) {
      font-size: 0.875rem;
     font-weight: 700;
    }
  }
}
