.modal {
  & .btn-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    z-index: 11;
    color: $yellow-color;
    opacity: 1;
    background: none;
    width: 25px;
    height: 25px;
    padding: 0;

    outline: none;
    cursor: pointer;

    &:focus,
    &:hover {
      outline: none;
      box-shadow: none;
    }

    &::before {
      content: '+';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 150%;
      font-weight: 300;
      color: $yellow-color;
      background-color: $gray-color;
      border-radius: 50%;

      transform: rotate(-45deg);

      @media (min-width: $sm) {
        color: $gray-color;
        background-color: $yellow-color;
      }
    }

    @media (min-width: $sm) {
      color: $yellow-color;
      width: 29px;
      height: 29px;
    }

    & .icon {
      width: 26px;
      height: 26px;
      fill: $yellow-color;
      stroke: $primary-color;

      @media (min-width: $sm) {
        fill: $yellow-color;
        stroke: $primary-color;
      }
    }
  }

  &--bg {
    & .modal-content {
      position: relative;
      background-color: $yellow-color;
      border-radius: $borderRadius;
      border: none;
      border: 1px solid rgba(0, 0, 0, 0.1);

      box-shadow: $boxShadowdark;
      overflow: hidden;

      @media (min-width: $sm) {
        padding: 1rem;
        min-height: 240px;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: 45%;
          background-image: var(--pic);
          background-repeat: no-repeat;
          background-position: 100% 0;
          background-size: cover;
          background-color: $primary-color;
        }
      }
    }

    & .modal-header {
      border: none;
      font-family: $mainFont;
      font-weight: 700;
    }

    & .title {
      width: calc(100% - 30px);
    }

    & .btn-close {
      z-index: 10;
      position: absolute;
      top: 1rem;
      right: 1rem;
      @include buttonReset();
      opacity: 1;
      color: $primary-color;
      background: none;

      display: flex;
      overflow: hidden;
      align-items: center;
      justify-content: center;
      width: 29px;
      height: 29px;

      @media (min-width: $sm) {
        color: $yellow-color;
        position: absolute;
        top: 2rem;
        right: 2rem;
      }

      & .icon {
        fill: $primary-color;
        stroke: $yellow-color;

        @media (min-width: $sm) {
          fill: $yellow-color;
          stroke: $primary-color;
        }
      }
    }

    & .modal-body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      padding: 1rem;
      font-family: $mainFont;

      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      color: $primary-color;

      @media (min-width: $md) {
        width: 50%;
        // justify-content: start;
        padding: 0 1rem;
      }

      & p {
        margin: 0;
        margin-bottom: 0.5rem;
      }

      & ul {
        margin: 0;
        margin-bottom: 1rem;
        padding-left: 0.6rem;
      }
    }
  }

  &--bg-sm {

    & .modal-dialog {
      max-width: 500px;
      width: 100%;
      margin: 1rem auto;

      @media (min-width: $sm) {
        max-width: 768px;
      }
    }

    & .modal-content {
      position: relative;
      background-color: $yellow-color;
      border-radius: $borderRadius;
      border: none;
      border: 1px solid rgba(0, 0, 0, 0.1);

      box-shadow: $boxShadowdark;
      overflow: hidden;

      @media (min-width: $sm) {

        &::before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: 45%;
          background-image: var(--pic);
          background-repeat: no-repeat;
          background-position: 100% 0;
          background-size: cover;
          background-color: $primary-color;
        }
      }
    }

    .title {
      width: calc(100% - 30px);
      font-size: 1.2rem;
      line-height: normal;
    }

    & .modal-header {
      border: none;
      font-family: $mainFont;
      font-weight: 700;
    }

    & .modal-body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      padding: 1.6rem 1rem;
      font-family: $mainFont;

      font-weight: 400;
      font-size: 1rem;
      line-height: normal;
      color: $primary-color;

      @media (min-width: $xs) {
        padding: 2rem;
      }

      @media (min-width: $sm) {
        width: 50%;
        padding: 3rem 1rem 3rem 3rem;
      }

      & p {
        margin: 0;
        margin-bottom: 0.5rem;
      }

      & .list {
        margin: 0;
        margin-bottom: 1rem;
        padding-left: 1rem;
        list-style: disc;

        & li {
          margin: 0;
        }
      }
    }
  }
}
