.page-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: $mainFontSize;
  line-height: $mainLineHeight;

  width: 100%;
  min-height: 100vh;

  background-color: $contrast-color;

  scroll-behavior: smooth;

  padding-top: 100px;
  @media (max-width: $xs) {
    padding-top: 80px;
  }

  & .container {
    position: relative;
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding-left: 16px;
    padding-right: 16px;

    @media (min-width: $md) {
      padding-left: 20px;
      padding-right: 20px;
    }
    @media (min-width: $xl) {
      padding-left: 80px;
      padding-right: 80px;
    }

  }



  & .link {
    text-decoration: none;

    &:focus,
    &:hover {
      text-decoration: none;
    }
  }
}


