:root {
  --slidePicMaxSize: 410px;
  --mainSlidePicMaxSize: 233px;

  @media (min-width: $xxxs) {
    --slidePicMaxSize: 540px;
    --mainSlidePicMaxSize: 240px;
  }
  @media (min-width: $md) {
    // --slidePicMaxSize: 39vw;
    --slidePicMaxSize: 600px;
    --mainSlidePicMaxSize: 500px;
  }

  @media (min-width: $xl) {
    --slidePicMaxSize: 575px;
  }
}

.slider {
  width: 100%;
  height: auto;
  border-radius: $borderRadius;

  overflow: hidden;

  &--main {
    & .swiper-container {
      overflow: hidden;
      position: relative;
      width: 99.4%;
      border-radius: $borderRadius;

      @media (min-width: 425px) {
        width: 99.2%;
      }
      @media (min-width: $xs) {
        width: 99.1%;
      }

      @media (min-width: $sm) {
        width: 99.5%;
        box-shadow: $boxShadow;
      }

      @media (min-width: $xxlg) {
        width: 99.4%;
      }
    }

    & .swiper-wrapper {
      height: auto;
      box-shadow: none;
    }
  }

  & .swiper-container {
    position: relative;
  }

  & .swiper-wrapper {
    height: auto;

    box-shadow: $boxShadow;
  }

  & .swiper-slide {
    height: 100%;

    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    will-change: transform;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    text-decoration: none;

    max-height: var(--slidePicMaxSize);

    border-radius: $borderRadius;

    overflow: hidden;

    & picture {
      width: 100%;
      height: auto;
    }

    & img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  & .swiper-button-next:after,
  & .swiper-button-prev:after {
    font-size: 1rem;

    @media (min-width: $sm) {
      font-size: 2.45rem;
    }
  }

  // overwrite swiper defaults
  & .swiper-pagination {
    bottom: 18px;
    left: 50%;
    transform: translateX(-50%);

    &-bullet {
      margin: 0 6px;
      background-color: $white-color;
      border: 2px solid #fff;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      opacity: 1;
    }

    &-bullet-active {
      background-color: transparent;
    }
  }

  & .swiper-button {
    &-container {
      background-color: rgba(0, 0, 0, 0.25);
    }

    &-prev,
    &-next {
      z-index: 4;
      color: $white-color;

      &:focus,
      &:hover {
        color: $yellow-color;
      }
    }
  }

  &--content {
    height: 360px;
    @media (min-width: $xs) {
      height: 400px;
    }
    @media (min-width: $sm) {
      height: 435px;
    }
    @media (min-width: $md) {
      height: 500px;
    }

    & .swiper-container {
      height: 100%;
    }
    & .swiper-wrapper {
      height: 100% !important;
    }

    & .swiper-slide {
      @media (min-width: $xs) {
        height: 400px;
      }
      @media (min-width: $sm) {
        height: 435px;
      }
      @media (min-width: $md) {
        height: 500px;
      }

      & img,
      & picture {
        height: 100%;
      }
    }
  }

  &--slide-br {
    & .swiper-slide {
      background-position: center;
      background-size: cover;
      border-radius: $borderRadius;
      overflow: hidden;
    }
  }

  &--sale {
    & .swiper-wrapper {
      height: 100% !important;
    }
    & .swiper-button-next {
      right: 10px;
      @media (min-width: $sm) {
        right: 17px;
      }
    }
    & .swiper-button-prev {
      left: 10px;
      @media (min-width: $sm) {
        left: 17px;
      }
    }
    & .swiper-button-next:after,
    & .swiper-button-prev:after {
      font-size: 1rem;

      @media (min-width: $sm) {
        font-size: 2.45rem;
      }
    }

    & .swiper-pagination-fraction {
      bottom: 10px;
      left: 35px;
      color: $white-color;
      font-family: $mainFontBold;
      font-size: 20px;
    }
  }
}

.slider.slider--main {
  height: 100%;

  & .swiper-slide {
    // max-height: var(--mainSlidePicMaxSize);
  }
}

.slider.slider--presale {
  & .swiper-wrapper {
    height: 100% !important;
  }
}

.slider.slider--bg {
  & .swiper-container,
  & .swiper-wrapper,
  & .swiper-slide {
    height: 100% !important;
    // background-size: contain;
  }
}
