.button {
  @include buttonReset();
  padding: 16px 20px;
  font-family: $mainFontBold;
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  transition: $transition-base;
  cursor: pointer;

  @include ff(
    'font-size',
    (
      375px: 16px,
      1280px: 20px,
    )
  );

  &--red {
    padding: 0.5rem 2rem;
    color: $white-color;
    border: 2px solid $red-color;
    background-color: $red-color;
    box-shadow: $boxShadowdark;
    border-radius: 16px;

    &:focus,
    &:hover {
      color: $red-color;
      background-color: transparent;
    }
  }

  &--outline {
    color: $white-color;
    border: 2px solid $white-color;
    border-radius: 16px;
    background-color: transparent;

    &:focus,
    &:hover {
      color: $yellow-color;
      border-color: $yellow-color;
      background-color: transparent;
    }
  }
  &--yellow {
    color: $primary-color;
    border: 2px solid $yellow-color;
    border-radius: 16px;
    background-color: $yellow-color;

    &:focus,
    &:hover {
      // color: $yellow-color;
      // background-color: transparent;
      border-color: $primary-color;
    }
  }
}
