.article-preview {
  height: 100%;
  border-radius: 1.38rem;
  box-shadow: -1px 4px 24px 0 rgba(0, 0, 0, 0.2);
  background: #fff;
  overflow: hidden;

  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;

  &:hover {
    box-shadow: -1px 4px 24px 0 rgba(0, 0, 0, 0.3);
    transform: translateY(-2px); // Добавил легкий подъем при hover

    & .article-preview__img {
      transform: scale(1.05); // Уменьшил масштаб для более плавного эффекта
    }
  }

  &__figure {
    margin: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    overflow: hidden;
  }

  &__img {
    display: block;
    width: 100%;
    height: 100%;
    max-height: 13rem;
    object-fit: cover;
    transition: transform 0.5s ease-in-out; // Убрал box-shadow, увеличил время анимации

    // Добавляем transition для обратного эффекта при уходе курсора
    will-change: transform; // Оптимизация для браузера

    @media (min-width: $sm) {
      max-height: 16rem;
    }
  }

  &__link {
    display: block;
    text-decoration: none;
    color: inherit;

    transition: 0.3s;

    // Убираем стандартные состояния ссылки
    &:focus {
      outline: none;

      & .article-preview__img {
        transform: scale(1.05);
      }
    }
  }

  &__content {
    padding: 1rem;
    margin-top: auto;
    margin-bottom: 24px;
    padding-bottom: 0;

    @media (min-width: $sm) {
      margin-bottom: 12px;
    }
  }

  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1rem;
    line-height: 120%;
    color: #000;

    @media (min-width: $sm) {
      font-size: 1.25rem;
    }
  }

  &__description {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    -webkit-line-clamp: 3;
    -moz-line-clamp: 3;
    -webkit-box-orient: vertical;
    -moz-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    font-weight: 400;
    font-size: 0.75rem;
    line-height: 120%;
    color: #000;
    margin-bottom: 0;

     @media (min-width: $sm) {
      -webkit-line-clamp: 2;
      -moz-line-clamp: 2;
    }
  }

  &__footer {
    padding: 1rem;
    padding-top: 0;
  }

  & .link-icon {
    position: relative;
    display: inline-block;
    padding-right: 2.5rem;
    font-weight: 400;
    font-size: 0.94rem;
    line-height: 140%;
    transition: 0.3s;

    &::before {
      content: '';
      position: absolute;
      top: 60%;
      right: 0;
      transform: translate(0, -50%);
      width: 2rem;
      height: 1rem;
      background-image: url('../img/pages/article/icon-arrow.svg');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }

    &:hover {
      padding-right: 3rem;
      transition: 0.3s;
    }
  }
}
