body,
html {
  padding: 0;
  margin: 0;
}

html {
  height: 100%;
}

body {
  // font-family: $mainFont;
  font-family: 'Avenir Next Cyr', sans-serif;
  font-size: 20px;
  line-height: $mainLineHeight;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

svg {
  fill: currentColor;
}

button[type='submit'] {
  cursor: pointer;
}

.small {
  font-size: 0.875rem;
  line-height: 100%;
}
