.form {
  display: flex;
  flex-direction: column;
  position: relative;

  &__policy {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: #8f8f8f;

    @media (min-width: $xs) {
      font-size: 13px;
      color: #42474d;
    }

    & a {
      color: inherit;
    }
  }

  &__check {
    display: flex;
    align-items: center;
    margin-top: 0.7rem;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: #8f8f8f;
    cursor: pointer;

    @media (min-width: $xl) {
      font-size: 14px;
    }

    &-text {
      display: inline-flex;
      flex-wrap: wrap;
      width: calc(100% - 1.3em);
      padding-left: 0.4rem;

      @media (min-width: $sm) {
        padding-left: 0.8rem;
      }
      @media (min-width: $xl) {
        padding-left: 0.5rem;
      }

      & span {
        display: block;
      }

      & a {
        color: inherit;
        text-decoration: revert;

        &:focus,
        &:hover {
          color: $primary-color;
        }
      }
    }

    @media (min-width: $md) {
      margin-top: 1rem;
      // justify-content: end;
    }
    @media (min-width: $xl) {
      justify-content: stretch;
    }

    .form-check-input {
      width: 1.3em;
      height: 1.3em;
    }
  }

  & .form-label,
  & .form-select,
  & .form-control {
    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 140%;
    color: $primary-color;
    padding-bottom: 0.75rem;
    margin-bottom: 0;

    outline-color: $primary-color;

    @media (min-width: $sm) {
      font-size: 15px;
    }

    @media (min-width: 1024px) {
      padding: 0 0 1rem;
    }

    @media (min-width: $xl) {
      padding-bottom: 1rem;
    }
  }

  &__label-name {
    display: block;
    padding-bottom: 0.25rem;
  }

  & .form-control {
    font-size: 17px;

    @media (min-width: $sm) {
      font-size: 15px;
    }
  }

  & .form-control::placeholder {
    font-size: 17px;
  }

  & .form-select,
  & .form-control {
    border-radius: 6px;
    border-color: transparent;
    padding: 0.75rem;
    padding-left: 1.45rem;

    @media (min-width: $xl) {
      padding-left: 1.65rem;
    }

    cursor: pointer;

    &:focus,
    &:hover {
      box-shadow: 0 0 0 0.15rem rgba(164, 176, 186, 0.22);
    }

    &::placeholder {
      font-family: $mainFont;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: rgba(66, 71, 77, 0.5);

      @include ff(
        'font-size',
        (
          320px: 17px,
          768px: 15px,
        )
      );

      @media (min-width: $xs) {
        font-size: 17px;
      }
    }
  }

  & .form-label {
    position: relative;
    padding-bottom: 0.75rem;
  }

  & .valid-feedback,
  & .invalid-feedback {
    position: absolute;
    bottom: -4px;
    right: 0;
    text-align: right;
  }

  & .form-check-input {
    margin: 0;
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }

  & .form-select {
    margin-bottom: 0;
    // min-height: 46px;

    font-family: $mainFont;
    font-style: normal;
    font-size: 17px;
    font-weight: 400;
    color: rgba(66, 71, 77, 0.5);

    & option {
      padding: 10px;
      color: $primary-color;
      min-height: 50px;
      font-size: 17px;
      line-height: 30px;
    }

    &:focus,
    &:active {
      color: $primary-color;
    }
  }

  &__btns {
    position: relative;
    display: flex;
    flex-direction: column;
    // align-items: flex-end;
    margin-top: auto;
    margin-bottom: auto;
    padding-right: 0;
    padding-top: 1rem;

    @media (min-width: $sm) {
      align-items: flex-start;
    }

    & .form-check-input:checked {
      background-color: #888888;
      border-color: #888888;
      margin: 0;
    }

    & button[type='submit'] {
      margin-top: 0;
      margin-bottom: 10px;
      padding: 13px 29px;
      max-width: 100%;
      width: 100%;
      text-align: center;
      @media (min-width: $xs) {
        width: auto;
      }
      @media (min-width: $xl) {
        padding-top: 0.8rem;
        padding-bottom: 0.8rem;
      }
      @media (min-width: $xxl) {
        margin-right: auto;
        margin-left: 0;

        padding: 0.8rem 1.6rem;
      }
    }
  }
}

.custom-form-check {
  display: flex;
  align-items: center;
  margin-top: 0.7rem;
  font-style: normal;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: normal;
  color: #8f8f8f;
  cursor: pointer;

  &:focus::before {
    cursor: pointer;
    border-color: $primary-color;
  }

  @media (min-width: $xl) {
    font-size: 14px;
  }

  & input {
    &:checked + label::before {
      z-index: 1;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      padding: 5px;
      background-image: url(../img/icons/check.svg);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      color: $white-color;
      font-weight: 700;
      background-color: #8f8f8f;
    }
  }

  & label {
    position: relative;
    display: block;
    padding-left: 30px;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid $primary-color;
      background-color: $white-color;
    }

    &:hover::before {
      cursor: pointer;
      border-color: $primary-color;
    }
    &:focus::before {
      cursor: pointer;
      border-color: $primary-color;
    }
  }

  & a {
    color: inherit;
    text-decoration: revert;

    &:focus,
    &:hover {
      color: $primary-color;
    }
  }
}

.was-validated {
  &:invalid {
    & .custom-form-check {
      & label::before {
        outline: 2px solid red;
      }
    }
  }
  &:valid {
    & .custom-form-check {
      & label::before {
        outline: 2px solid green;
      }
    }
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}
