.page-header {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  background-color: $primary-color;
  padding: 1.8rem 0 1.2rem;
  transition: $transition-base;

  @media (min-width: $sm) {
    padding: 1.6rem 0;
  }
  @media (min-width: $md) {
    padding: 1.3rem 0 1.1rem;
  }
  @media (min-width: $xl) {
    padding: 1.7rem 0 1.4rem;
  }

  & .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    flex-direction: row-reverse;

    @media (min-width: $md) {
      flex-direction: row;
    }
  }

  & a {
    align-items: center;
    color: $white-color;
  }

  & .phone {
    width: 30px;

    @media (max-width: $sm) {
      position: absolute;
      top: 0;
      right: 16px;
    }

    @media (min-width: $sm) {
      width: 50px;
      padding-right: 21px;
    }
    @media (min-width: $md) {
      width: auto;
      padding-right: 0;
    }

    & span {
      display: none;

      @media (min-width: $md) {
        display: flex;
      }
    }

    & .icon {
      width: 14px;
      height: 24px;
      margin-left: auto;

      @media (min-width: $sm) {
        width: 17px;
        height: 27px;
      }
      @media (min-width: $md) {
        width: 14px;
        height: 24px;
      }
    }
  }

  &__lk {
    display: none;
    @media (min-width: $md) {
      display: flex;
    }

    & span {
      margin-left: 0;
    }
  }

  & .logo {
    @media (max-width: $sm) {
      margin: 0 auto;
    }

    @media (min-width: $sm) {
      width: 230px;
      height: 40px;
    }

    @media (min-width: $md) {
      width: auto;
      height: auto;
    }
  }

  &__btn-menu {
    @include buttonReset();
    display: flex;
    color: $white-color;

    @media (max-width: $sm) {
      position: absolute;
      top: 0;
      left: 16px;
    }

    @media (min-width: $md) {
      display: none;
    }

    & .icon {
      width: 30px;
      height: 30px;
      fill: $white-color;

      @media (min-width: $sm) {
        width: 50px;
      }
    }
  }
}

.scroll-down .page-header {
  transform: translate3d(0, -100%, 0);
}

.scroll-up .page-header {
  // padding: 2rem 0;
  transform: none;
}

.scroll-up .page-header {
  color: $primary-color;
}

.scroll-up:not(.menu-open) .page-header {
  background-color: $primary-color;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.25);
}
