.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  border-radius: $borderRadius;
  box-shadow: $boxShadow;
  background-color: $blue-color;

  width: 100%;

  text-decoration: none;
  color: $primary-color;

  padding: 10% 2rem;

  &:focus,
  &:hover {
    color: $primary-color;
    text-decoration: none;
  }

  @media (min-width: $lg) {
    margin-top: 0;
    width: 50%;

    // padding: var(--box-pd);
    padding: 25% 2rem;
  }

  @media (min-width: $xl) {
    // min-width: 295px;
    padding-bottom: 2rem;
  }

  &__title {
    position: relative;
    margin: auto;
    padding-top: calc(25% + 0.5rem);
    padding-bottom: 5%;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    width: 100%;
    max-width: 220px;

    @media (min-width: $xl) {
      padding-bottom: 2%;
      padding-top: calc(30% + 0.5rem);
    }

    @media (min-width: $xxl){
      font-size: 22px;
      padding-top: calc(40% + 0.5rem);
    }


    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: 50% 0;
    }

    & span {
      display: block;
      text-transform: uppercase;
    }
  }

  &--job {
    align-items: flex-end;
    @media (min-width: $sm) and (max-width: 800px) {
      width: 47vw !important;
    }

    & .box__title {
      &::before {
        width: 27%;
        height: 35%;
        min-width: 94px;
        background-image: url('../img/icons/job.svg');

        @media (min-width: $xxl){
          min-width: 120px;
          height: 38%;
        }
      }
    }
  }

  &--schedule {
    align-items: flex-end;
    height: 100%;

    @media (min-width: $sm) and (max-width: 800px) {
      width: 47vw !important;
    }

    & .box__title {
      &::before {
        width: 30%;
        height: 43%;
        min-width: 50px;
        background-image: url('../img/icons/schedule-2.svg');

        @media (min-width: $xxl){
          min-width: 70px;
          height: 47%;
        }
      }

      & span {
        display: block;
        text-transform: lowercase;
      }
    }
  }

  &__link {
    margin: auto 0 0 auto;

    & span {
      margin: 0;
    }
  }

  &:focus,
  &:hover {
    background-color: $yellow-color;

    & .link-text--more .icon {
      width: 40px;
      transform: translateX(6px);
      transition: $transition-base;
    }
  }

  &--yellow {
    background-color: $yellow-color;

    &:focus,
    &:hover {
      background-color: $blue-color;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    padding: 26px 57px 29px 33px;

    @media (min-width: $lg) {
      padding: 0;
    }
  }
}
