// Глобальные переменные

$mainFont: 'Avenir Next Cyr', 'Arial', 'Helvetica CY', sans-serif;
$mainFontBold2: 'Avenir Next Cyr Bold', 'Arial', 'Helvetica CY', sans-serif;
$mainFontMedium: 'Avenir Next Cyr Medium', 'Arial', 'Helvetica CY', sans-serif;
$mainFontBold: 'Avenir Next Cyr Demi', 'Arial', 'Helvetica CY', sans-serif;
$baseFontLight: 'Avenir Next Cyr Light', 'Arial', 'Helvetica CY', sans-serif;

// colors
$white-color: #ffffff;
$contrast-color: #f5f5f5;
$primary-color: #42474d;

$red-color: #cd4141;
$blue-color: #e2f0fb;
$yellow-color: #fffeda;
$green-color: #31a428;
$gray-color: #8f8f8f;
$gray-light-color: #90959a;
$city-list-color: #052a56;
$placeholder-color: rgba(66, 71, 77, 0.5);
$modal-bg: rgba(66, 71, 77, 0.8);
$card-bg: #f6f6f6;
$bg: #f5f7f8;

$prestige-color: #e3c2fce2;
$premier-color: #fcd7acca;


$transition-base: 0.3s;

// Size
$mainFontSize: 20px;
$mainLineHeight: 130%;

$borderRadius: 16px; // 16px
$borderRadius22: 22px; // 16px
$boxShadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;
$boxShadowdark: 4px 4px 8px rgba(0, 0, 0, 0.25);
$boxShadowdarkInner: inset 0 -3em 1em rgba(0, 0, 0, 0.1),
  0 0 0 2px rgb(255, 255, 255), 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);

$w-size: 1440px;

$xxxs: 420px;
$xxs: 480px;
$xs: 576px;
$xsm: 767px;
$sm: 768px;
$md: 992px;
$xl: 1200px;
$lg: 1300px;
$xxl: 1400px;
$xlg: 1600px;
$xxlg: 1920px;

:root {
  --field-width: 100%;
  --form-offers-mr-sl: 1rem;

  --nav-menu-mb: 0.75rem;
  --nav-menu-pt: 30px;

  --btn-padding: 0.5rem;
  --bs-body-line-height: 130%;
  --nav-btn-pd: 19px;

  --size-clubs-result-h: 300px;

  // page-club
  --form-book-w: 100%;
  --slider-club-w: 100%;
  --slider-club-h: 365px;
  // club end

  --page-sale-mb: 30px;
  --page-sale-mr: 0.25rem;
  --page-sale-count: 2;
  --page-sale-w-size: calc(100% / var(--page-sale-count) - var(--page-sale-mr));

  --box-pd-base: 37px;
  --box-pd: 37px 1rem;

  @media (min-width: 375px) {
    // club
    --slider-club-h: 427px;
    // club end
  }

  @media (min-width: $sm) {
    --nav-btn-pd: 20px;
    --nav-menu-pt: 1.4rem;
  }

  @media (min-width: $md) {
    --field-width: calc(50% - 0.5rem);
    // club
    --slider-club-h: 539px;
    --form-book-w: 30%;
    --slider-club-w: calc(70% - 10px);
    // club end

    --size-clubs-result-h: 650px;

    --page-sale-mb: 50px;
    --page-sale-mr: 1rem;
    --page-sale-count: 4;
  }
  @media (min-width: $xl) {
    --btn-padding: 0.7rem 1rem;
    --nav-btn-pd: 20px 24px 19px 24px;
    --bs-modal-border-radius: 14px;

    // club
    --slider-club-h: 501px;
    --slider-club-w: 70%;
    // club end

    --size-clubs-result-h: 850px;

    --box-pd: 0 55px 30px 37px;
  }

  @media (min-width: $lg) {
    --page-sale-count: 4;

    --box-pd: 0 30px 37px;
  }

  @media (min-width: $xxl) {
    // club
    --form-book-w: 30%;
    --slider-club-w: 70%;
    --slider-club-h: 575px;
    // club end
  }
}

$popover-arrow-color: $primary-color;
$popover-header-bg: $white-color;

@keyframes animBg {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.2);
  }
}
@keyframes animBgRev {
  from {
    transform: scale(1.2);
  }

  to {
    transform: scale(1);
  }
}
