.articles {
  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 120%;
    letter-spacing: 0.02em;
    margin: 20px auto;

    @media (min-width: $md) {
      text-align: center;
      font-size: 2.25rem;
      margin: 36px auto;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    @media (min-width: $xs) {
      display: grid;
      gap: 2rem;
      grid-auto-rows: 1fr;
      align-items: stretch;
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: $md) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
  }
}
