.nav-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 1rem;
  padding: 1.5rem 0 1.1rem;
  padding-top: var(--nav-menu-pt);
  padding-bottom: calc(var(--nav-menu-pt) - var(--nav-menu-mb));

  @media (max-width: $xs) {
    padding-left: 16px;
    padding-right: 16px;
  }

  @media (min-width: $sm) {
    padding-top: var(--nav-menu-pt);
    padding-bottom: calc(var(--nav-menu-pt) - var(--nav-menu-mb));
  }

  @media (min-width: $xl) {
    align-items: start;
  }

  &.container {
    @media (min-width: $md) {
      margin-bottom: 15px;
    }
    @media (min-width: $xl) {
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;

    @include listReset();

    @media (max-width: 450px) {
      justify-content: space-between;
    }

    @media (min-width: $md) {
      flex-wrap: nowrap;
      align-items: center;
      gap: 0.5rem;
    }
    @media (min-width: $lg) {
      flex-wrap: wrap;
      gap: 0.75rem;
    }
  }

  &__item {

    @media (max-width: 450px) {
      flex-grow: 1;
      width: calc(50% - 0.35rem);
    }

    &:nth-child(2) {

      @media (min-width: 450px) {
        max-width: 325px;
      }

      @media (min-width: $xs) {
        width: auto;
      }

    }

    &.order-1.order-md-3 {
      @media (max-width: 450px) {
        width: 25%;
      }
    }
    &.order-3 {
      @media (max-width: 450px) {
        width: 40%;
      }
    }
  }

  &__link {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    padding: var(--nav-btn-pd);
    font-family: inherit;
    font-weight: 400;
    font-size: 0.88rem;

    line-height: 130%;

    color: $primary-color;
    background-color: $blue-color;
    box-shadow: $boxShadow;
    border-radius: $borderRadius;
    transition: $transition-base;

    text-decoration: none;

    @media (min-width: $md) {
      font-size: 1.12rem;
    }


    &:focus,
    &:hover {
      color: $primary-color;
      background-color: $yellow-color;
      text-decoration: none;

      // & svg {
      //   fill: $primary-color;
      //   stroke: $white-color;
      // }
    }

    & span {
      padding-right: 12px;
      width: calc(100% - 20px);
    }

    &.active {
      background-color: $yellow-color;
      color: $primary-color;
    }

    &--sale {
      & span {
        padding-right: 20px;
      }
    }

    &--outline {
      background-color: transparent;
      color: $primary-color;
      box-shadow: none;
      border: 1px solid;

      &:hover,
      &.active {
        background: linear-gradient(176deg, #e9876c 0, #da6240 76.4%, #db7255 100%);
        color: #fff;

      }
    }

    

    @media (min-width: $lg) {
    }

    & svg {
      // fill: $primary-color;
      width: 16px;
      height: 16px;
      margin-left: auto;

      @media (min-width: $xs) {
        width: 20px;
      height: 20px;
      }
    }
  }

  & .link-pay {
    margin-bottom: var(--nav-menu-mb);
  }
}
