.page-footer {
  margin-top: auto;
  margin-bottom: 0;
  padding-bottom: 1.6rem;
  padding-top: 2.2rem;
  background-color: $primary-color;
  color: $white-color;

  @media (min-width: $sm) {
    padding-top: 2.2rem;
    padding-bottom: 2rem;
  }
  @media (min-width: $md) {
    padding-top: 2.4rem;
  }

  &__top {
    display: grid;
    grid-template-areas:
      'phone'
      'links'
      'support'
      'logo';

    @media (min-width: $sm) {
      grid-template-columns: 1fr 1fr;
      column-gap: 3%;
      grid-template-areas:
        'logo support'
        'phone support '
        'links  links';
    }

    @media (min-width: $md) {
      grid-template-columns: 1fr 3fr;
      column-gap: 5%;
      grid-template-areas:
        'phone links'
        'logo links '
        'support  links';
    }

    @media (min-width: $xl) {
      grid-template-columns: 1fr 4fr 1fr;
      column-gap: 20px;
      grid-template-areas:
        'logo links support'
        'phone links support '
        '.    links support ';

      align-content: space-between;
    }

    @media (min-width: $xxl) {
      column-gap: 50px;
      row-gap: 5px;
    }
  }

  & .logo {
    grid-area: logo;

    @media (min-width: $sm) {
      margin-bottom: 0.5rem;
      padding-left: 7px;
    }
    @media (min-width: $md) {
      margin-bottom: 0.5rem;
      padding-left: 0;
    }
  }

  &__phone {
    grid-area: phone;
    display: flex;
    align-items: center;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    /* identical to box height, or 26px */

    text-transform: uppercase;
    margin-bottom: 1.7rem;

    @media (min-width: $xs) {
      margin-bottom: 1rem;
    }
    @media (min-width: $sm) {
      padding-left: 7px;
    }

    @media (min-width: $md) {
      margin-bottom: 0;
      padding-left: 0;
    }

    & .icon {
      width: 47px;
      height: 37px;
      margin-right: 14px;
      @media (min-width: $sm) {
        display: none;
        margin-right: 0;
      }
    }
  }

  &__links {
    flex-grow: 1;
    grid-area: links;

    margin-bottom: 0.4rem;

    @media (min-width: $xs) {
      margin-bottom: 1rem;
    }
    @media (min-width: $sm) {
      padding-top: 1.2rem;
    }

    @media (min-width: $md) {
      padding-top: 0;
    }
  }

  &__support {
    grid-area: support;
    margin-bottom: 1.6rem;
    font-size: 15px;
    line-height: 21px;

    @media (min-width: $sm) {
      margin-bottom: 1rem;
      padding-top: 0.9rem;
    }

    @media (min-width: $md) {
      padding-top: 0;
    }

    @media (min-width: $xxl) {
      padding-left: 34px;
      padding-top: 3px;
    }

    & span {
      display: block;
      @media (min-width: $sm) {
        display: inline-block;
      }
      @media (min-width: $xl) {
        display: block;
      }
    }

    & a {
      padding-left: 0.2rem;
      text-decoration: none;
      color: inherit;

      &:focus,
      &:hover {
        color: $yellow-color;
      }
    }
  }

  &__buttom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    flex-direction: column;

    padding: 0.5rem 0;
    padding-right: 2rem;
    // padding-top: 0.3rem;
    margin-top: 1.1rem;
    font-family: $mainFont;
    font-weight: 300;
    font-size: 14px;
    line-height: 120%;

    border-top: 1px solid $white-color;

    // 
    padding-top: 1rem;

    @media (min-width: $sm) {
      flex-direction: row;
      padding-right: 0;
      // padding-top: 0.9rem;
      padding-top: 2rem;
      margin-top: 1.2rem;

      //
    align-items: center;
    }
    @media (min-width: $md) {
      margin-top: 2rem;
    }

    @media (min-width: $xl) {
      margin-top: 17px;
    }

    @media (min-width: $xxl) {
      margin-top: 35px;
    }

    & a {
      margin-bottom: 10px;
    }

    & > * {
      @media (min-width: $sm) {
        // width: 25%;
      }
    }

    & .link-text {
      @media (min-width: $sm) and (max-width: 800px) {
        width: 24%;
        // padding-top: 18px;
      }
      @media (min-width: $md) {
        // padding-top: 1.6rem;
      }
    }
  }

  &__copy {
    line-height: 144%;

    @media (min-width: $sm) and (max-width: 900px) {
      // padding-top: 0.7rem;
    }
  }

  & .social {
    // padding-top: 18px;
  }
}
