.nav-city {
  position: relative;
  height: 100%;

  @media (max-width: 450px) {
    width: 100%;
  }

  & .title {
    margin-bottom: 15px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
  }

  &__title {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    @include buttonReset();

    padding: var(--nav-btn-pd);
    font-family: inherit;
    font-weight: 400;
    font-size: 1rem;

    height: 100%;

    color: $primary-color;
    background-color: $blue-color;
    box-shadow: $boxShadow;
    border-radius: $borderRadius;
    transition: $transition-base;
    line-height: normal;

    text-decoration: none;
    cursor: pointer;

    // @include ff(
    //   'font-size',
    //   (
    //     375px: 14px,
    //     768px: 14px,
    //     1200px: 20px,
    //   )
    // );

    // @include ff(
    //   'font-size',
    //   (
    //     320px: 14px,
    //     375px: 18px,
    //     450px: 20px,
    //     768px: 14px,
    //     1000px: 16px,
    //     1200px: 15px,
    //     1280px: 17px,
    //     1400px: 20px,
    //   )
    // );

    @media (max-width: 450px) {
      width: 100%;

      justify-content: space-between;
    }

    @media (min-width: $xl) {
      line-height: 130%;
    }

    @media (min-width: $lg) {
      margin-left: 0;
    }

    &:focus,
    &:hover {
      color: $primary-color;
      background-color: $yellow-color;

      & svg {
        fill: $primary-color;
        stroke: $white-color;
      }
    }

    & ~ .nav-city__list.d-block {
      background-color: $yellow-color;
    }

    & svg {
      margin-left: 0.5rem;
    }

    & span {
      white-space: nowrap;
      // max-width: 94px;
      overflow: hidden;

      @media (min-width: 375px) {
        // max-width: 120px;
      }
      @media (min-width: $sm) {
        max-width: 120px;
      }
      @media (min-width: $xxl) {
        max-width: 200px;
      }
    }

    &--outline {
      background-color: transparent;
      color: $primary-color;
      box-shadow: none;
      border: 1px solid;

      &:hover,
      &.active {
        background: linear-gradient(176deg, #e9876c 0, #da6240 76.4%, #db7255 100%);
        color: #fff;

        & svg {
          fill: white;
        }
      }
    }
  }

  &__wrapper {
    z-index: 10;
    position: absolute;
    top: 75%;
    left: 0;

    padding: 1rem;
    padding-right: 0.2rem;
    border-radius: $borderRadius;
    background-color: $yellow-color;

    box-shadow: $boxShadowdark;
    padding-bottom: 0.1rem;

  }

  &__list {
    @include listReset();

    & li {
      min-width: 180px;
      margin-bottom: 0.25rem;

      @media (min-width: 375px) {
        min-width: 200px;
      }
      @media (min-width: $sm) {
        min-width: 175px;
      }
      @media (min-width: $xl) {
        min-width: 200px;
      }

      &:first-child a {
        font-weight: 700;
      }
    }

    & a {
      display: block;
      font-family: $mainFont;
      text-decoration: none;
      color: $primary-color;
      transition: $transition-base;

      font-weight: 400;
      font-size: 18px;
      line-height: 130%;

      padding-bottom: 0.1rem;

      @media (min-width: 375px) {
        font-size: 20px;
      }

      @media (min-width: $sm) {
        font-size: 17px;
      }
      @media (min-width: $xl) {
        font-size: 20px;
      }

      &:focus,
      &:hover {
        font-family: $mainFontBold;
        font-weight: 700;
        transition: $transition-base;
      }

    }
  }
}

.my-custom-scrollbar {
  height: 300px;
  overflow-y: auto;
  overflow-x: hidden;

  @media (min-width: $md) {
    height: auto;
  }

  /* Scrollbar */
  &::-webkit-scrollbar {
    width: 0.45rem;
  }
  &::-webkit-scrollbar-thumb,
  &::-webkit-scrollbar-track-piece {
    background: rgba(66, 71, 77, 0.15);
    border-radius: 41px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
}
