.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  list-style: none;
  padding: 1rem 0;

  &__item {
    position: relative;
    font-weight: 400;
    font-size: 1rem;
    line-height: 100%;
    color: #42474c;

    &:not(:last-child)::before {
      content: '/';
      position: absolute;
      right: -0.5rem;
      top: 50%;
      transform: translate(0, -50%);
      font-size: 1rem;
      line-height: 130%;
      color: #42474c;
    }

    &:last-child {
      color: rgba(66, 71, 76, 0.6);
      &::before {
        display: none;
      }
    }

    @media (min-width: $md) {
      line-height: 130%;
    }
  }

  &__link {
    text-decoration: none;
    color: inherit;
  }
}
