.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @include listReset();

  margin-bottom: 1.2rem;

  &__item:not(:last-child) {
    margin-right: 0.8rem;

    @media (min-width: $sm) {
      margin-right: 0.3rem;
    }
  }

  &__link {
    display: block;
    color: $white-color;

    &:focus,
    &:hover {
      color: $yellow-color;
    }

    &--vk .icon {
      width: 42px;
      height: 24px;

      @media (min-width: $sm) {
        width: 31px;
        height: 18px;
      }
    }

    &--inst .icon {
      width: 40.65px;
      height: 28.45px;

      @media (min-width: $sm) {
        width: 30px;
        height: 21px;
      }
    }

    &--fb .icon {
      width: 35px;
      height: 35px;

      @media (min-width: $sm) {
        width: 26px;
        height: 26px;
      }
    }

    &--support {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 5px;
      width: 40px;
      height: 40px;
      border: 1px solid white;
      border-radius: 7px;
    }

    &--support .icon {
      width: 25px;
      height: 25px;
      margin-left: 2px;
    }
  }

  & .icon {
    fill: $white-color;
  }
}
