.section {
  border-radius: $borderRadius;
  box-shadow: $boxShadowdark;

  &--bg-pic {
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    max-width: 550px;
    min-height: 254px;
    padding: 1rem;
    padding-top: 5rem;
    background-color: $primary-color;


    transition: $transition-base;
    background-size: cover;

    text-decoration: none;
    overflow: hidden;

    @media (min-width: $xs) {
      min-height: 254px;
    }
    @media (min-width: $sm) {
      padding: 2.5rem 1.5rem 1rem 2.075rem;
    }
    @media (min-width: $xxl) {
      padding: 2.5rem 2.075rem;
    }

    // &::before {
    //   content: '';
    //   position: absolute;
    //   z-index: -1;
    //   top: 0;
    //   left: 0;
    //   width: 100%;
    //   height: 100%;
    //   background: linear-gradient(
    //       209.04deg,
    //       rgba(0, 0, 0, 0) 55.12%,
    //       rgba(0, 0, 0, 0.7) 86.38%
    //     ),
    //     var(--pic);
    //   background-repeat: no-repeat;
    //   background-position: 50%;
    //   background-size: cover;

    //   @media (min-width: $md) {
    //     animation: animBgRev 1s ease-in-out;
    //   }

    //   transition: 1s;
    // }

    & .section__pic-bg {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      @media (min-width: $md) {
        animation: animBgRev 1s ease-in-out;
      }

      & picture,
      & img {
        display: block;
        width: 100%;
        min-height: 254px;


        @media (min-width: $md) {
          height: 100%;
        }
      }
    }

    &:focus,
    &:hover {
      transition: $transition-base;
      text-decoration: none;
      outline: none;

      &::before,
      & .section__pic-bg {
        @media (min-width: $md) {
          animation: animBg 1s forwards;
        }
      }

      & .link-text--more .icon {
        width: 40px;
        transform: translateX(6px);
        transition: $transition-base;

        @media (max-width: $xs) {
          width: 19px;
        }
      }
    }

    & .title {
      @include px-to-rem(25, margin-bottom);
      color: $white-color;

      @media (min-width: $md) {
        min-height: 52px;
      }
    }

    & .link-text--more {
      margin-left: auto;
      margin-right: 0;
      color: $white-color;

      &:focus,
      &:hover {
        color: $yellow-color;
      }

      & .icon {
        @media (max-width: $xs) {
          width: 19px;
        }
      }
    }
  }
}
