.mobile-menu {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;

  background-color: rgba(0, 0, 0, 0.5);

  min-height: 110vh;

  box-shadow: $boxShadowdark;

  &.active {
    display: flex;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 1rem;
    padding-top: 2rem;
    background-color: $primary-color;
    height: 100%;
    min-height: 110vh;
    min-width: 320px;
  }

  &__btn-close {
    position: absolute;
    top: 1rem;
    right: 1.2rem;
    @include buttonReset();

    display: flex;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    color: $yellow-color;

    & .icon {
      fill: $yellow-color;
      stroke: $primary-color;
    }
  }

  & .link-pay {
    padding-top: 13px;
    padding-bottom: 13px;
    font-size: 20px;
    box-shadow: $boxShadowdark;


  }

  & .list {
    margin: 0.5rem 0;

    & a {
      display: block;
      padding: 0.25rem 0;
    }
  }

  & .link-text svg {
    color: $yellow-color;
  }

  & a {
    margin: 0.5rem 0;
    text-decoration: none;
  }

  & hr {
    background-color: $white-color;
    width: 100%;
    height: 2px;
  }
}
