.link-text {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: $white-color;
  text-decoration: none;
  font-family: inherit;

  &:focus,
  &:hover {
    color: $yellow-color;
    text-decoration: none;
  }

  &--svg {
    & .icon {
      @include px-to-rem(7, margin-right);
      fill: $yellow-color;
    }
  }

  &--more {
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: $primary-color;
    line-height: normal;
    text-transform: lowercase;

    & .icon {
      margin-top: 0.2rem;
      margin-left: 0.5rem;
      height: 8px;
    }

    &:focus,
    &:hover {
      color: $primary-color;
    }
  }

  &--arrow {
    & span {
      width: calc(100% - 16px - 0.5rem);
    }

    & .icon {
      margin-right: 0.5rem;
      fill: $yellow-color;
    }
  }
}
