.nav-select {
  padding: 0 1rem;
  margin: 0.5rem;

  color: $primary-color;
  background-color: $blue-color;
  box-shadow: $boxShadow;
  border-radius: $borderRadius;
  transition: $transition-base;
  line-height: normal;

  cursor: pointer;

  &:focus {
    border: 1px solid black;
  }

  &:focus,
  &:hover {
    background-color: $yellow-color;
  }

  & select {
    padding: 0.5rem 0;
    background-color: transparent;
    cursor: pointer;
    outline: none;

    font-size: 16px;

    border: none;

    &:focus {
      outline: none;
    }

    @media (min-width: $lg) {
      padding: 0.7rem 0;
      font-size: 20px;
    }
  }

  & option {
    padding: 0.5rem;
    background-color: $yellow-color;

    &:hover {
      background-color: #fff;
    }
  }
}
