.article__form {
  width: 100%;
  max-width: 865px;
  margin: 0 auto;
  margin: 35px auto;
  padding: 1.25rem;
  background: #e2f0fb;

  @media (min-width: $md) {
    margin: 50px auto;
    border-radius: 1rem;
    box-shadow: -8px -8px 10px 0 #fff, 8px 8px 16px 0 rgba(73, 76, 79, 0.19);
  }
}

.article-form {
  max-width: 430px;
  margin: 0 auto;

  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 100%;
    margin-bottom: 1rem;

    @media (min-width: $sm) {
      margin-bottom: 26px;
      font-size: 1.75rem;
      line-height: 120%;
      text-align: center;
    }
  }

    & .form-personal {
    margin-bottom: 0.5rem;
    font-weight: 400;
    font-size: 0.81rem;
    line-height: 140%;
    color: inherit;

    @media (min-width: $md) {
      text-align: center;
      font-size: 0.69rem;
    }

    & a {
      color: inherit;
      text-decoration: underline;
      text-decoration-skip-ink: none;
    }
  }

  & .form-control,
  & .form-select {
    border-radius: 0.75rem;
  }

  & .form-control {
    padding: 0.62rem 1.56rem 0.75rem;
  }

  & .form-select {
    border-radius: 0.75rem;
    padding: 0.6rem 1.56rem 0.6rem;
  }

  & .form__btns {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    gap: 10px;
    padding-top: 0;

    & .button {
      flex-grow: 1;
      margin: auto;
      border-radius: 1rem;
      padding: 15px 22px;
      margin-bottom: 1rem;
      box-shadow: none;

      @media(max-width: $xs) {
        width : 100%;
      }
    }
  }
}

.article-form-modal {

  .btn-close {
    & svg {
      display: none;
    }

    &::before {
      font-size: 250%;
      color: rgba(0, 0, 0, .25);
      background-color: transparent;
      left: -30%;
    }
  }

  & .modal-content {
    border-radius: 0.75rem;
    max-width: 400px;
    margin: 0 auto;
  }

  & .modal-body {
    padding-top: 2rem;
    font-weight: 700;
    font-size: 0.75rem;
    line-height: 100%;
    text-align: center;

    border-radius: 0.75rem;
    box-shadow: 0 4px 11px 0 rgba(0, 0, 0, 0.25);
    background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);

      & svg {
        z-index: 0;
        width: 36px;
        height: 36px;
        fill: #e1ebf0;
      }

  }

  & .form-message-modal {
  padding-bottom: 0;
  }

  & .form-message-modal__title {
    padding: 1rem;
    margin-top: -1rem;
    font-weight: 700;
    font-size: 0.88rem;
    line-height: 100%;
    text-align: center;
    border-radius: 0.38rem;
    border: 2px solid #3080a3;
  }
  & .form-message-modal__text {
    padding: 1rem;
  }

  & .social {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1rem;

    &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 0.5rem;

      & .icon {
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }


      &--vk {
        & .icon {
          background-image: url('../img/pages/club-tariffs/icons/icon-vk.svg');
        }
      }
      &--dz {
        & .icon {
          background-image: url('../img/pages/club-tariffs/icons/icon-dzen.svg');
        }
      }
      &--tm {
        & .icon {
          background-image: url('../img/pages/club-tariffs/icons/icon-telegram.svg');
        }
      }

      & a {
        display: block;
      }
    }
  }
}
