.city-list {
  @include listReset();

  @media (min-width: $md) {
    column-count: 2;
  }

  & li {
    min-width: 100px;
    margin-bottom: 4px;

    &:first-child {
      & button {
        font-weight: 700;
      }
    }
  }

  & a {
    text-decoration: none;
    color: $primary-color;
  }

  & button {
    @include buttonReset();
    display: block;
    color: $city-list-color;

    font-family: $mainFont;
    font-weight: 400;
    font-size: 18px; // 15px
    line-height: 140%;

    width: 100%;
    text-align: left;
    padding: 2px;

    @include ff(
      'font-size',
      (
        320px: 20px,
        768px: 18px,
      )
    );

    &:focus,
    &:hover {
      font-family: $mainFontBold;
      font-weight: 700;
    }
  }
}
