.link-icon {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-decoration: none;
  font-family: inherit;

  color: $primary-color;

  &:focus,
  &:hover {
    text-decoration: none;
    color: $primary-color;
  }

  &--more {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    transition: $transition-base;

    &:focus,
    &:hover {
      & .icon {
        width: 40px;
        transform: translateX(6px);
        transition: $transition-base;
      }
    }

    & .icon {
      margin-top: 0.2rem;
      margin-left: 0.5rem;
      height: 8px;
      transition: $transition-base;
    }
  }

  &--bold {
    font-family: $mainFontBold;
  }

  &--lower {
    text-transform: lowercase;
  }
}
