.logo {
  display: block;
  width: 126px;
  height: 33px;

  @media (min-width: $md) {
    width: 230px;
    height: 39px;
  }
  @media (min-width: $lg) {
    width: 269px;
    height: 45px;
  }

  &--footer {
    width: 154px;
    height: 25px;

    & picture {
      width: 100%;
      height: 100%;
    }
  }
}
